import React from 'react'
import { ADD_ERROR, DELETE_ERROR } from '../actions/types';

const errorReducer = (state = [], action) => {
    switch (action) {
        case ADD_ERROR:
            return [...state, action.payload]
        case DELETE_ERROR:
            state = []
            return;
        default:
            return state;
    }
}

export default errorReducer
