import React, { useState } from 'react'
import { useDispatch } from 'react-redux';
import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import { GetdeafultCity } from '../../../Redux/actions/weatherActions';
import Alert from '@mui/material/Alert';



const AutocompleteComponent = (e) => {
    const dispatch = useDispatch()
    const autoCompleteURL = process.env.REACT_APP_AUTOCOMPLETE_URL
    const api_key = process.env.REACT_APP_API_KEY
    const [suggestions, setSuggestions] = useState([])
    const [erros, setError] = useState(true)

    const serachCity = (e) => {
        let value = e.target.value
        let english = /[^A-Za-z]/ig

        if (!english.test(value)) {
            setError(true)
            fetch(`${autoCompleteURL}${api_key}&q=${value}`)
                .then((res) => res.json())
                .then((response) => setSuggestions(response))

        }
        else {
            setError(false)
        }
    }
    return (
        <div className="autoComplete">
            {erros ? "" : <Alert severity="error">English letters only.</Alert>}

            <Autocomplete
                id="free-solo-demo"
                size="medium"
                sx={{ width: 500 }}
                options={suggestions?.map((option) => option.LocalizedName)}
                onChange={(e, value) => dispatch(GetdeafultCity(value))}
                renderInput={(params) =>
                    <TextField onChange={serachCity} {...params} label="Search city" key={params.Key} />}
            />
        </div>
    )
}

export default AutocompleteComponent
