import React, { useEffect } from 'react'
import DayCardComponent from '../Card/DayCardComponent'
import { useSelector, useDispatch } from 'react-redux'
import { addFavorite, deleteFavorite } from '../../../Redux/actions/favoritesAction';
import Button from '@mui/material/Button';

const WeekContainer = () => {


    const dispatch = useDispatch()
    const weekWeather = useSelector((state) => state.weather.weekly)
    const currentLocation = useSelector((state) => state.location)
    const currentWeather = useSelector((state) => state.weather.current)
    const favorites = useSelector((state) => state.favorites)


    const checkIfFavorite = () => {
        if (favorites.some(e => e.id === currentLocation.cityKey)) {
            return true
        }
        else {
            return false
        }
    }
    return (

        <div className="container">

            <h5 className="display-5 text-muted">{currentLocation.city}</h5>
            <h5 className="display-5 text-muted">{currentWeather.WeatherText}</h5>
            {/* <h5 className="display-5 text-muted">{currentWeather?.Temperature.Metric?.Value}</h5> */}
            {checkIfFavorite() ? <Button variant="outlined" onClick={() => dispatch(deleteFavorite(currentLocation.cityKey))}>Unsubscribe</Button>
                : <Button variant="outlined" onClick={() => dispatch(addFavorite(currentLocation.cityKey, currentLocation.city))}>Subscribe</Button>
            }
            <h1 className="display-4 jumbotron">5-Day Forecast.</h1>
            <div className="row justify-content-center">

                {weekWeather.map((day, index) => {
                    return <DayCardComponent day={day} index={index} />
                })}

            </div>
        </div>
    )
}

export default WeekContainer
